import React from 'react';

class Searcher extends React.Component {
  renderBySearch() {
    const searchedData = document.getElementById("searchTerm").value.toLowerCase();

    const companyData = this.props.data.companyData.slice();  
    const dividedCompanyData = this.gatherSearchedInfo(companyData);

    const identifiedCompanyData = this.identifyCompanyData(dividedCompanyData, searchedData);
    const finalCompanyData = this.gatherfinalCompanyData(identifiedCompanyData, companyData);
    
    this.setState({
      changedInfoBySearch: finalCompanyData,
      options : { 
        changedInfoBySearch: true,
        changedInfoByCost: false, 
        changedInfoByPopularity: false, 
        changedInfoByRate: false
      }
    }, () => {this.props.updateState(this.state)})
  };

  gatherSearchedInfo(companyData) { 
    let splitedInfo;
    let gatheredSeachedInfo = [];

    companyData.forEach((company) => {
      let searchedInfo = {};

      for (let companyInfo in company) {
        if (companyInfo === "companyName" || companyInfo === "serviceType") {
          splitedInfo = company[companyInfo].split(" "); 
          if (!(companyInfo in searchedInfo)) {  
            searchedInfo[companyInfo] = splitedInfo;
            splitedInfo.forEach((value, index) => {
              splitedInfo[index] = value.toLowerCase();
            })
            searchedInfo[companyInfo].push(company[companyInfo].toLowerCase())
          }
        }
      }
      gatheredSeachedInfo.push(searchedInfo)
    })
    return gatheredSeachedInfo;
  };

  identifyCompanyData(dividedCompanyData, searchedData) { 
    let identifiedCompanyData = [];
    
    dividedCompanyData.forEach((companyInfo) => {
      let searchedInfo = {};
      for (let companyInfoProp in companyInfo) {
        companyInfo[companyInfoProp].forEach((searchedText) => { 
          if (searchedData && !(companyInfoProp in searchedInfo)) {
            if (searchedData === searchedText.substring(0, searchedData.length)) {             
                searchedInfo[companyInfoProp] = companyInfo[companyInfoProp].slice(-1)[0]; // ???
            }
          }     
        })
      }
      if (!(Object.entries(searchedInfo).length === 0 && searchedInfo.constructor === Object)) {
        identifiedCompanyData.push(searchedInfo)
      }
    });

    if (!identifiedCompanyData.length) {
      dividedCompanyData.forEach((companyInfo) => {
        let searchedInfo = {};
        for (let companyInfoProp in companyInfo) {
          searchedInfo[companyInfoProp] = companyInfo[companyInfoProp].slice(-1)[0]; // ???
        }   
        identifiedCompanyData.push(searchedInfo)
      })
    }

    return identifiedCompanyData  
  };

  gatherfinalCompanyData(identifiedCompanyData, companyData) {
   let finalCompanyData = [];
   companyData.forEach((company) => {
      identifiedCompanyData.forEach((companyInfo, i) => {
        if (identifiedCompanyData[i] && Object.keys(companyInfo)[0] in company) {
          if(companyInfo[Object.keys(companyInfo)[0]] === company[Object.keys(companyInfo)[0]].toLowerCase()) {
            finalCompanyData.push(company);
          }
        }
      })
    })
    if (!finalCompanyData.length) {
      finalCompanyData = companyData[companyData.length - 1];
    }; 
    return finalCompanyData;
  }

  render() {
    return (
      <div className ="search">
        <input type="text" className ="searchTerm" id = "searchTerm" placeholder="What are you looking for?" onChange = {() => {this.renderBySearch()}}/>
        <button type="submit" className ="searchButton">
          <i className ="fas fa-search"></i>
        </button>
      </div>
    )
  }
}

export default Searcher;