import React from "react";

class TableSorter extends React.Component {
  renderByCost() {
    const minCost = document.getElementById("min-cost").value;
    let maxCost = document.getElementById("max-cost").value;

    let sortedInfo = [];
    
    this.props.data.changedInfoBySearch.forEach((companyData) => {
      if ("serviceCost" in companyData) { 
        if (!maxCost) {
          maxCost = companyData["serviceCost"];
        } 
        if (companyData["serviceCost"] >= minCost && companyData["serviceCost"] <= maxCost){
          sortedInfo.push(companyData);
        }
      }
    })

    this.setState({ 
      changedInfoByCost: sortedInfo, 
      options: {
        changedInfoByCost: true,
        changedInfoBySearch: false, 
        changedInfoByRate: false,
        changedInfoByPopularity: false
      }
    }, () => {this.props.updateState(this.state)})
  }

  renderByRate(event, sortByParam) {
    const companyData = this.props.data;
    const changedData = event.target.name;

    if(event.target.checked) {
      for (let option in companyData.options) {
        if (companyData.options[option]) {
          let sortedData = companyData[option].slice();
          companyData[changedData] = sortedData.sort((comparedCompany1, comparedCompany2) => {
            return comparedCompany2[sortByParam] - comparedCompany1[sortByParam]
          })
        }
      }
    }
    else {
      for (let option in companyData.options) {
        if (companyData.options[option]) {
          let sortedData = companyData[option].slice();
          companyData[changedData] = sortedData.sort((comparedCompany1, comparedCompany2) => {
            return comparedCompany1.id - comparedCompany2.id
          })
        }
      }
    }
    
    this.setState({ 
      [changedData]: companyData[changedData], 
      options: {
        changedInfoBySearch: false,
        changedInfoByRate: changedData === "changedInfoByRate" ? true : false,
        changedInfoByPopularity: changedData === "changedInfoByPopularity" ? true : false,
        changedInfoByCost: false
      }
    }, () => {this.props.updateState(this.state)})
  }  

  render() {
    return (
      <div className="filter-wrapper">
        <form>
          <div className="filter-block">
            <h4>Sort by</h4>
            <label className="container">Popular
              <input type="checkbox" name = "changedInfoByPopularity" onChange = {(event) => {this.renderByRate(event, "totallyBooked")}}/>
              <span className="checkmark"></span>
            </label>
            <label className="container">High rate
              <input type="checkbox" name = "changedInfoByRate" onChange = {(event) => {this.renderByRate(event, "rate")}}/>
              <span className="checkmark"></span>
            </label> 
          </div>
          <div className="filter-block">
            <h4>Cost</h4>
            <div className="cost-container">
              <div>
              <span>Min</span>
                <input type="number" id="min-cost" placeholder="from" onChange = {() => {this.renderByCost()}} autoComplete="off" required/>
              </div>
              <div>
              <span>Max</span>
                <input type="number" id="max-cost" placeholder="to" onChange = {() => {this.renderByCost()}} autoComplete="off" required/>
              </div>
            </div>
          </div>
        </form>
      </div> 
    )
  }
}

export default TableSorter;